import React from 'react';
import styles from './Select.module.css';

export type Option = {
    label: string;
    value: string | number;
};

export type SelectProps = {
    label: string;
    options: Option[];
    onChange: (value: string | number) => void;
    value?: string | number;
    disabled?: boolean;
    error?: boolean;
    helperText?: string;
    size?: 'small' | 'medium' | 'large';
    fullWidth?: boolean;
};

const Select: React.FC<SelectProps> = ({label, options, value, onChange, disabled = false, error = false, helperText = '', size = 'medium', fullWidth = false,
                                       }) => {
    const handleChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
        onChange(event.target.value);
    };

    const selectClass = `select ${size} ${error ? 'error' : ''} ${fullWidth ? 'fullWidth' : ''}`;

    return (
        <div className="select-container">
            <label className="select-label">
                {label}
                <select
                    className={selectClass}
                    value={value}
                    onChange={handleChange}
                    disabled={disabled}
                >
                    {options.map((option) => (
                        <option key={option.value} value={option.value}>
                            {option.label}
                        </option>
                    ))}
                </select>
            </label>
            {helperText && <p className={`helper-text ${error ? 'error-text' : ''}`}>{helperText}</p>}
        </div>
    );
};

export default Select;
